<template>
    <div class="comments-main">
        <div class="comments-list" v-if="comments.length > 0">
            <div class="header-comments">
                <span>{{ comments.length }}条评论</span>
                <el-switch v-model="method" active-text="最新" inactive-text="最热"></el-switch>
            </div>
            <el-scrollbar max-height="400px" ref="scrollbarRef">
                <transition-group name="comment-list-transition" tag="div" class="comment-list">
                    <div class="comment" v-for="(comment, index) in comments" :key="index">
                        <div class="comment-header">
                            <router-link :to="`/Athor/${comment.author.id}`" class="commenter-info">
                                <img :src="comment.author.image" alt="User Image" class="commenter-image">
                                <span class="commenter-name">{{ comment.author.name }}</span>
                            </router-link>
                            <span class="comment-create-time">{{ change_date(comment.create_time) }}</span>
                        </div>
                        <div class="comment-body">
                            <p class="comment-content">{{ comment.content }}</p>
                        </div>
                        <div class="comment-actions">
                            <button class="action-button" @click="Thumbs_up(comment.id, index, 'no')" :disabled="Reply.isSendingAgree"> 
                                {{ comment.agree }} 人赞同
                            </button>
                            <button class="action-button" @click="ShowReply(index)">查看 {{ comment.replys.length }} 条回复</button>
                        </div>
                        <div class="replys" v-show="showReplyIndex === index"> 
                            <hr>
                            <div v-if="comment.replys.length">
                                <div class="reply" v-for="(content, i) in comment.replys" :key="i">
                                    <div class="comment-header">
                                        <router-link :to="`/Athor/${content.author._id}`" style="text-decoration: none; color:black" class="reply-info">
                                            <img :src="content.author.image" class="replyer-image">
                                            <span class="replyer-name">{{ content.author.name }}</span>
                                        </router-link>
                                        <span class="reply-create-time">{{ change_date(content.create_time) }}</span>
                                    </div>
                                    <div class="reply-body">
                                        <p class="reply-content">{{ content.content }}</p>
                                    </div>
                                    <div class="reply-actions">
                                        <button class="reply-action-button" @click="Thumbs_up(content.id, index, i)" :disabled="Reply.isSendingAgree"> 
                                            {{ content.agree }} 人赞同
                                        </button>
                                    </div>
                                </div>
                            </div>
                            <div style="display: flex; margin-top: 16px;">
                                <el-input
                                    type="text"
                                    v-model="Reply.ReplyContent"
                                    placeholder="理性发言,友好互动"
                                    autosize
                                    :min-rows="3"
                                    :max-rows="10"
                                    style="width: 100%; margin-right: 8px;"
                                ></el-input>
                                <el-button type="primary" @click="createReply(comment.id, index)" :disabled="Reply.isSending" style="font-size: 1.3em;">回复</el-button>
                            </div>
                            <button class="Collapse" @click="CollapseReply(index)">^ 收起</button>
                        </div>
                    </div>
                </transition-group>
            </el-scrollbar>
            <div class="comment-bottom">
            </div>
        </div>
        <el-empty v-else description="更多留言等你来说"></el-empty>
        <div class="create-comment">
            <div style="display: flex;">
                <el-input
                    type="textarea"
                    v-model="newComment"
                    placeholder="理性发言,友好互动"
                    autosize
                    :min-rows="3"
                    :max-rows="10"
                    style="width: 100%; margin-right: 8px;"
                />
                <el-button type="primary" @click="create_comment" :disabled="loading" style="font-size: 1.3em;">评论</el-button>
            </div>
        </div>
    </div>
</template>
<script setup>
import { ref, onMounted, defineProps, nextTick,watch } from 'vue';
import { get, post } from '@/http/httpRequest';
import { useUrl } from '@/store/useUrl';
import { notice } from '../notice';
import { useUser } from '@/store/useUser';

const props = defineProps({
    postId: {
        type: [String, Number],
        required: true
    }
});

const postId = props.postId;
const urls = useUrl();
const user = useUser();
const comments = ref([]);
const newComment = ref("");
const loading = ref(false);
const method = ref(true);
const scrollbarRef = ref(null);
const Reply = ref({
    isShowReplys: false,
    ReplyContent: "",
    isSending: false,
    isSendingAgree: false
});
const showReplyIndex = ref(-1);

const get_comments = async () => {
    let url = `${urls.comment_list}/${postId}/1`;
    const response = await get(url);
    if (response) comments.value = response.data.slice().sort((a,b)=>{return new Date(a.create_time)<new Date(b.create_time)});
};

const create_comment = async () => {
    if (!newComment.value.trim().length) {
        notice("不能发送空内容!!!", "", 1500);
        return null;
    }
    loading.value = true;
    const url = `${urls.create_comment}/${postId}`;
    const response = await post(url, { content: newComment.value });
    if (response) {
        notice("发布成功", "等待审核中", 1500);
        newComment.value = '';
        nextTick(() => {
            if (scrollbarRef.value && scrollbarRef.value.wrap) {
                scrollbarRef.value.setScrollTop(scrollbarRef.value.wrap.scrollHeight);
            }
        });
    }
    loading.value = false;
};

const change_date = (str) => {
    try {
        const date = new Date(str);
        return date.toLocaleDateString();
    } catch (e) {
        return str;
    }
};

const Thumbs_up = async (commentId, index, i) => {
    Reply.value.isSendingAgree = true;
    const url = urls.comment_agree + '/' + commentId;
    const response = await get(url);
    if (response) {
        if (i === 'no') {
            comments.value[index].agree++;
        } else {
            comments.value[index].replys[i].agree++;
        }
    }
    Reply.value.isSendingAgree = false;
};

const ShowReply = (index) => {
    showReplyIndex.value = index;
};

const CollapseReply = (index) => {
    if (showReplyIndex.value === index) {
        showReplyIndex.value = -1;
    }
};

const createReply = async (commentId, index) => {
    Reply.value.isSending = true;
    const url = urls.create_commentComments + '/' + commentId;
    const response = await post(url, { content: Reply.value.ReplyContent });
    if (response) {
        notice("回复成功","等待审核中",1500);
    }
    Reply.value.ReplyContent = '';
    Reply.value.isSending = false;
};

onMounted(async () => {
    await get_comments();
    user.initData();
});

watch(()=>method.value,(newMethod)=>{
    if(! newMethod) comments.value=comments.value.sort((a,b)=>{
        return a.create_time>b.create_time;
    });
    else comments.value=comments.value.sort((a,b)=>{
        return a.agree>b.agree
    });
});
</script>
