<template>
    <div class="post-main" v-if="post">
        <div class="post-card">
            <div class="content-main">
                <p class="title"><strong>{{ post.title }}</strong></p>
                <pre class="body">{{ post.content }}</pre>
            </div>
            <hr>
            <div class="bottom">
                <div class="metadata">
                    <span class="create-time">{{ change_date(post.create_time) }}</span> |
                    <span class="views">浏览 {{ post.views_count }}</span>
                </div>
                <div class="author" v-if="post.author">
                    <router-link :to="`/Athor/${post.author.id}`">
                        <img :src="post.author.image" alt="Author Image" class="author-image" loading="lazy"/>
                    </router-link>
                    <p class="name"><em>{{ post.author.username }}</em></p>
                </div>
            </div>
            <div class="comments-main">
                <button @click="toggleComments" id="offShow">^ 查看评论</button>
                <transition name="fade">
                    <div v-if="isCommentsVisible && post.comments_status" class="comments-section">
                        <GetComments :postId="postId" />
                    </div>
                    <div v-else-if="!post.comments_status" class="comments-disabled">
                        作者关闭了评论区
                    </div>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useUrl } from '@/store/useUrl';
import { useRoute, useRouter } from 'vue-router';
import { get } from '@/http/httpRequest';
import GetComments from '@/components/comments/GetComments.vue';

export default {
    components: {
        GetComments,
    },
    setup() {
        const urls = useUrl();
        const router = useRouter();
        const route = useRoute();
        const postId = route.params.postId;
        const post = ref({});
        const isCommentsVisible = ref(false);

        const initPage = async () => {
            let url = urls.postDetail + `/${postId}`;
            const response = await get(url);
            if (response) {
                post.value = response.data;
            } else {
                router.replace({ path: "/404" });
            }
        };

        const toggleComments = () => {
            isCommentsVisible.value = !isCommentsVisible.value;
        };

        const change_date = (str) => {
            try {
                const date = new Date(str);
                return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`;
            } catch (e) {
                return str;
            }
        };

        onMounted(async () => {
            await initPage();
        });

        return {
            post,
            change_date,
            postId,
            isCommentsVisible,
            toggleComments
        };
    }
};
</script>
